.clause--box-component {
    border: 1px solid rgba(62, 62, 62, 0.2);
    min-height: 500px;
    max-height: 500px;
    overflow-y: scroll;
    padding: 0.5rem 0.7rem;
    line-height: 1.5;
    font-size: 12px;

    @media (min-width: 768px) {
        line-height: 1.7;
        padding: 0.7rem 1rem;
        font-size: 13px;
    }

    table {
        width: 100%;
        border: 1px solid rgba(62, 62, 62, 0.2);
        border-collapse: collapse;
        text-align: center;
        font-size: 11px;
        margin: 0.3rem 0;

        @media (min-width: 768px) {
            font-size: 12px;
        }

        th,
        td {
            border: 1px solid rgba(62, 62, 62, 0.2);
            padding: 0.2rem;
        }
    }
}
