.order--bundle-component {
    font-size: 12px;
    margin-top: 3rem;
    margin-bottom: 3rem;

    @media (min-width: 768px) {
        font-size: 14px;
    }

    .confirm--button--component {
        min-width: 65px;
        max-width: 65px;
        max-height: 26px;
        margin-left: 0;
        font-size: 11px;

        @media (min-width: 768px) {
            min-width: 90px;
            max-width: 90px;
            max-height: 30px;
            font-size: 13px;
        }
    }

    .head {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 40px;
        padding: 0.5rem;
        border-top: 5px solid rgba(62, 62, 62, 1);
        border-bottom: 1px solid rgba(112, 112, 112, 1);
        background-color: rgb(244, 244, 244);

        @media (min-width: 768px) {
            min-height: 50px;
        }

        .left {
            flex: 1;

            p.order--number {
                .date {
                    font-size: 0.75em;
                    margin-top: 0.3rem;
                    opacity: 0.8;
                    display: block;

                    @media (min-width: 768px) {
                        display: inline;
                        margin-left: 0.5rem;
                    }

                    &::before {
                        content: '[ ';
                    }

                    &::after {
                        content: ' ]';
                    }
                }
            }
        }

        .right {
            min-width: 120px;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            @media (min-width: 768px) {
                min-width: 250px;
            }

            .total--amount {
                &::before {
                    content: ' ';
                }

                span {
                    font-size: 0.75em;
                    color: rgba(62, 62, 62, 0.78);

                    &::before {
                        content: '';
                        margin-right: 0.5rem;
                        font-size: 1.35em;
                        color: rgba(62, 62, 62, 1);

                        @media (min-width: 768px) {
                        }
                    }
                }
            }

            .detail-btn {
                position: absolute;
                top: -36px;
                right: 0;

                @media (min-width: 768px) {
                    position: relative;
                    top: initial;
                    margin-left: 1rem;
                }
            }
        }
    }

    ul.list {
        li {
            display: flex;
            padding: 0.8rem 0.4rem;
            border-bottom: 1px solid rgba(112, 112, 112, 0.3);

            .first {
                min-width: 28px;

                @media (min-width: 768px) {
                    min-width: 40px;
                }
            }

            .second {
                min-width: 33px;
                font-size: 11px;

                @media (min-width: 768px) {
                    font-size: 13px;
                    min-width: 45px;
                }
            }

            .third {
                flex: 1;

                figure {
                    width: 60px;
                    height: 80px;
                    display: flex;

                    @media (min-width: 768px) {
                        width: 100px;
                        height: 120px;
                    }

                    img {
                        width: 100%;
                        object-fit: cover;
                    }

                    figcaption {
                        margin-left: 0.7rem;
                        white-space: nowrap;

                        p.name {
                            margin-bottom: 0.5rem;
                        }

                        span.price {
                            &::after {
                                content: '';
                            }
                        }

                        small {
                            font-size: 11px;
                            margin-left: 0.3rem;
                            opacity: 0.8;

                            &::before {
                                content: ' [';
                            }
                            &::after {
                                content: ']';
                            }
                        }
                    }
                }
            }

            .fourth {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                min-width: 70px;
                font-size: 11px;

                @media (min-width: 768px) {
                    min-width: 100px;
                    font-size: 13px;
                }

                .state {
                    margin-bottom: 0.5rem;
                }

                .cancle {
                    color: rgb(190, 23, 23);
                }
            }
        }
    }

    .under {
        margin-top: 0.4rem;

        @media (min-width: 768px) {
            margin-top: 1rem;
        }
    }
}
