section#point {
    table {
        width: 100%;
        font-size: 12px;
        margin-bottom: 2rem;

        @media (min-width: 768px) {
            font-size: 14px;
            margin-bottom: 4rem;
        }

        thead,
        tbody {
            tr {
                th,
                td {
                    padding: 1rem 0;
                    border-right: 1px solid rgb(112, 112, 112);

                    &:last-child {
                        border-right: 0;
                    }
                }
            }
        }

        thead {
            tr {
                font-weight: bold;
                border-top: 5px solid rgb(62, 62, 62);
                border-bottom: 1px solid rgba(112, 112, 112, 1);
            }
        }

        tbody {
            tr {
                border-bottom: 1px solid rgba(112, 112, 112, 0.1);

                &:last-child {
                    border-bottom: 1px solid rgba(112, 112, 112, 1);
                }

                td {
                    text-align: center;

                    &:nth-child(2) {
                        text-align: left;
                        padding-left: 0.8rem;
                    }

                    &:nth-child(3) {
                        font-weight: bold;
                    }
                }
            }
        }
    }
}
