section#illustrate {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background-image: url('/assets/images/illustrate/illust-bg.svg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    .header--container {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;

        .wrap {
            max-width: 1100px;
            margin: 0 auto;
            padding: 1rem;
        }

        .logo {
            width: 30px;
        }

        .right {
            width: 30px;
            float: right;
        }
    }

    article.contents--container {
        position: absolute;
        top: 0;
        left: 50%;
        width: 100%;
        max-width: 500px;
        height: 100vh;
        transform: translate(-50%, 0);
        background-image: url('/assets/images/illustrate/road.svg');
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;

        .sidedish,
        .chicken,
        .early,
        .lunchbox,
        .catering {
            position: absolute;
            width: 100%;
            height: min-content;

            .image,
            .name {
                position: absolute;

                img {
                    width: 100%;
                    object-fit: contain;
                }
            }

            .name {
                .writing,
                .word {
                    position: absolute;
                }

                .writing {
                    img {
                        display: none;

                        @media (min-width: 768px) {
                            display: block;
                        }
                    }
                }
            }
        }

