section#refering-nomember-detail,
section#refering-detail,
section#nomember,
section#personal,
section#point,
section#refering {
    .wrapper {
        display: flex;
        flex-direction: column;
        margin-top: 2rem;

        @media (min-width: 768px) {
            margin-top: 3rem;
        }

        @media (min-width: 1366px) {
            flex-direction: row;
            margin-top: 4rem;
        }

        .contents--box {
            position: relative;
            flex: 1;

            @media (min-width: 1366px) {
                padding-left: 2rem;
            }

            .box {
                .empty--component {
                    border-top: 5px solid rgba(62, 62, 62, 1);
                    border-bottom: 1px solid rgba(112, 112, 112, 1);
                    text-align: center;
                    padding: 12rem 0;
                    font-size: 12px;

                    @media (min-width: 768px) {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}
