section#refering-nomember-detail,
section#refering-detail {
    .head {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 40px;
        padding: 0.5rem;
        border-top: 5px solid rgba(62, 62, 62, 1);
        border-bottom: 1px solid rgba(112, 112, 112, 1);
        background-color: rgb(244, 244, 244);

        @media (min-width: 768px) {
            min-height: 50px;
        }

        .left {
            flex: 1;

            p.order--number {
                .date {
                    font-size: 0.75em;
                    margin-top: 0.3rem;
                    opacity: 0.8;
                    display: block;

                    @media (min-width: 768px) {
                        display: inline;
                        margin-left: 0.5rem;
                    }

                    &::before {
                        content: '[ ';
                    }

                    &::after {
                        content: ' ]';
                    }
                }
            }
        }

        .right {
            min-width: 120px;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            @media (min-width: 768px) {
                min-width: 250px;
            }

            .total--amount {
                &::before {
                    content: ' ';
                }

                span {
                    font-size: 0.75em;
                    color: rgba(62, 62, 62, 0.78);

                    &::before {
                        content: '';
                        margin-right: 0.5rem;
                        font-size: 1.35em;
                        color: rgba(62, 62, 62, 1);

                        @media (min-width: 768px) {
                        }
                    }
                }
            }
        }
    }

    table {
        width: 100%;

        thead,
        tbody {
            tr {
                height: 50px;
                th,
                td {
                    vertical-align: middle;

                    &:first-child,
                    &:nth-child(3) {
                        width: 10%;
                    }

                    &:nth-child(2) {
                        width: 60%;
                    }

                    &:last-child {
                        width: 20%;
                    }
                }
            }
        }

        thead {
            tr {
                border-bottom: 1px solid rgb(112, 112, 112);

                th {
                    font-weight: bold;
                }
            }
        }

        tbody {
            tr {
                border-bottom: 1px solid rgba(112, 112, 112, 0.2);

                &:last-child {
                    border-bottom: 1px solid rgb(112, 112, 112);
                }

                td {
                    padding: 0.5rem 0;
                    text-align: center;

                    &:nth-child(2) {
                        text-align: left;

                        figure {
                            width: 60px;
                            height: 80px;
                            display: flex;
                            align-items: center;

                            @media (min-width: 768px) {
                                width: 100px;
                                height: 120px;
                            }

                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                            }

                            figcaption {
                                margin-left: 0.7rem;
                                white-space: nowrap;
                            }
                        }
                    }

                    &:last-child {
                        .price {
                            &::after {
                                content: '';
                            }
                        }
                    }
                }
            }
        }
    }

    ul.order--board {
        position: relative;
        margin: 4rem 0 1rem;

        @media (min-width: 768px) {
            margin: 5rem 0 1rem;
        }

        li {
            min-height: 60px;
            display: flex;
            border-bottom: 1px solid rgb(112, 112, 112);
            font-size: 12px;

            @media (min-width: 768px) {
                font-size: 14px;
            }

            &:first-child {
                min-height: 40px;
                border-bottom: 5px solid rgb(62, 62, 62);
                font-weight: bolder;
                font-size: 14px;

                @media (min-width: 768px) {
                    font-size: 15px;
                }
            }

            .title {
                width: 100%;
                max-width: 90px;
                padding: 23px 0;
                display: flex;
                // align-items: center;
                background-color: rgb(244, 244, 244);
                justify-content: center;
                font-weight: bolder;

                @media (min-width: 768px) {
                    max-width: 277px;
                    justify-content: flex-start;
                    padding-left: 2rem;
                }
            }

            .description {
                position: relative;
                flex: 1;
                display: flex;
                align-items: center;
                padding-left: 1rem;

                @media (min-width: 768px) {
                    padding-left: 2rem;
                }
            }
        }
    }
}
