hgroup.mypage--title-component {
    h2 {
        position: relative;
        font-size: 18px;
        font-weight: bolder;
        margin-bottom: 2rem;

        @media (min-width: 768px) {
            font-size: 24px;
            margin-bottom: 3rem;
        }

        span.swich {
            margin-left: 2rem;
            font-weight: normal;

            @media (min-width: 768px) {
                margin-left: 3rem;
            }

            a {
                font-size: 12px;
                opacity: 0.4;

                @media (min-width: 768px) {
                    font-size: 13px;
                }

                &.active {
                    opacity: 1;
                }

                &:first-child {
                    &::after {
                        content: '';
                        height: 100%;
                        border-right: 1px solid rgba(112, 112, 112, 0.5);
                        margin: 0 1rem;

                        @media (min-width: 768px) {
                            margin: 0 1.5rem;
                        }
                    }
                }
            }
        }

        .point--box {
            margin-top: 1rem;

            @media (min-width: 768px) {
                margin-top: 2rem;
            }

            .equal {
                display: inline-block;
                width: 15px;
                height: 10px;
                margin-right: 1rem;
                border-top: 2px solid rgb(62, 62, 62);
                border-bottom: 2px solid rgb(62, 62, 62);

                @media (min-width: 768px) {
                    width: 20px;
                    height: 12px;
                    margin-right: 1.3rem;
                }
            }

            .price.cyan {
                display: inline-block;
                font-size: 0.85em;

                &::after {
                    content: 'point';
                    color: rgb(62, 62, 62);
                    margin-left: 0.5rem;
                    font-weight: normal;
                }
            }
        }
    }
}
