section#personal {
    ul.order--board {
        position: relative;
        margin-top: 4rem;

        @media (min-width: 768px) {
            // margin: 5rem 0 1rem;
        }

        &.error {
            li {
                &.caution {
                    border: 0;
                    min-height: initial;
                }
            }
        }

        li {
            min-height: 60px;
            display: flex;
            border-bottom: 1px solid rgb(112, 112, 112);
            font-size: 12px;

            @media (min-width: 768px) {
                font-size: 14px;
            }

            &:first-child {
                min-height: 40px;
                border-top: 5px solid rgb(62, 62, 62);
                font-weight: bolder;
                font-size: 14px;

                @media (min-width: 768px) {
                    font-size: 15px;
                }
            }

            .title {
                width: 100%;
                max-width: 90px;
                padding: 23px 0;
                display: flex;
                // align-items: center;
                background-color: rgb(244, 244, 244);
                justify-content: center;
                font-weight: bolder;

                @media (min-width: 768px) {
                    max-width: 277px;
                    justify-content: flex-start;
                    padding-left: 2rem;
                }
            }

            .description {
                position: relative;
                flex: 1;
                display: flex;
                align-items: center;
                padding-left: 1rem;

                @media (min-width: 768px) {
                    padding-left: 2rem;
                }
            }
        }
    }

    .leave-btn {
        position: absolute;
        top: -40px;
        right: 0;
        height: 30px;
        font-weight: normal;

        @media (min-width: 768px) {
            top: -45px;
            height: 34px;
        }
    }

    .revise-btn {
        max-width: 350px;
        margin: 0;
        margin: 3rem auto 0;

        @media (min-width: 768px) {
            margin: 4rem auto 0;
        }
    }
}
